import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { createCustomer, fetchCustomer } from "@/lib/api/customerApi";

export interface User {
  _id: string;
  id: string;
  name: string;
  email: string;
  joinedDate: string;
  location: string;
  phone: string;
  image: string;
}

interface CustomerResponse {
  customers: User[];
  message?: string;
  success?: boolean;
}

interface customerState {
  customers: User[];
  loading: boolean;
  error: string | null;
}

const initialState: customerState = {
  customers: [],
  loading: false,
  error: null,
};

const customerSlice = createSlice({
  name: "customers",
  initialState,
  reducers: {
    clearCustomerState: () => initialState,
  },

  extraReducers: (builder) => {
    builder.addCase(createCustomer.pending, (state) => {
      state.loading = true;
      state.error = null;
    });

    builder.addCase(fetchCustomer.pending, (state) => {
      state.loading = true;
      state.error = null;
    });

    builder.addCase(
      createCustomer.fulfilled,
      (state, action: PayloadAction<CustomerResponse>) => {
        state.loading = false;
        state.customers = action.payload.customers;
      }
    );

    builder.addCase(createCustomer.rejected, (state, action) => {
      state.loading = false;
      state.error =
        (action.payload as string) ||
        action.error.message ||
        "Failed to Create Customer";
    });

    builder.addCase(
      fetchCustomer.fulfilled,
      (state, action: PayloadAction<CustomerResponse>) => {
        state.loading = false;
        state.customers = action.payload.customers;
      }
    );
    builder.addCase(fetchCustomer.rejected, (state, action) => {
      state.loading = false;
      state.error =
        (action.payload as string) ||
        action.error.message ||
        "Failed to fetch Customer";
    });
  },
});

export const { clearCustomerState } = customerSlice.actions;
export default customerSlice.reducer;
